@import url(https://fonts.googleapis.com/css2?family=Kumbh+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body,body div,span,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,abbr,address,cite,code,del,dfn,em,img,ins,kbd,q,samp,small,strong,sub,sup,var,b,i,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,figure,footer,header,hgroup,menu,nav,section,time,mark,audio,video{margin:0;padding:0;border:0;outline:0;font-size:100%;vertical-align:baseline;background:transparent;font-family:"Kumbh Sans",sans-serif;color:#1e1f36}h1{font-weight:700;font-size:32px;line-height:32px;text-transform:uppercase;margin-bottom:24px}.App{max-width:1440px;width:100%;margin:0 auto}
.FaqContainer{width:100%;min-height:768px;background:linear-gradient(180deg, #b068e9 0%, #6463e7 100%);display:flex;justify-content:center;align-items:center}@media screen and (max-width: 920px){.FaqContainer{padding:125px 0;min-height:auto}}.FaqContainer .Row{max-width:920px;width:100%;min-height:509px;background:#fff;margin:0 auto;display:flex;justify-content:space-between;box-shadow:0px 50px 50px -20px rgba(53,18,122,.497159);border-radius:23px}@media screen and (max-width: 920px){.FaqContainer .Row{flex-direction:column;align-items:center;margin:0 24px}.FaqContainer .Row::after{position:absolute;content:"";background:url(/faq-accordion-card/static/media/illustration-woman-online-mobile.0ec5a893.svg);width:100%;height:25%;background-repeat:no-repeat;top:20px;right:25px;background-position-x:center;background-size:auto}.FaqContainer .Row::before{position:absolute;content:"";background:url(/faq-accordion-card/static/media/bg-pattern-mobile.55aee912.svg);width:100%;height:20%;background-repeat:no-repeat;top:125px;right:5px;background-position-x:center;background-size:auto}}.FaqContainer .Row .Column{width:50%}@media screen and (max-width: 920px){.FaqContainer .Row .Column{width:80%;display:flex;flex-direction:column;justify-content:center;align-items:center}}.FaqContainer .Row .Column.Img{display:flex;justify-content:left;align-items:center;position:relative}@media screen and (max-width: 920px){.FaqContainer .Row .Column.Img{justify-content:center;height:70px;width:100%}}.FaqContainer .Row .Column.Img .Img{display:flex;justify-content:center;align-items:center;position:relative;width:84%;height:100%;background:url(/faq-accordion-card/static/media/bg-pattern-desktop.f51e07a2.svg);background-position-x:right;background-size:auto;background-position-y:center}@media screen and (max-width: 920px){.FaqContainer .Row .Column.Img .Img{display:none}}.FaqContainer .Row .Column.Img .Img::before{content:url(/faq-accordion-card/static/media/illustration-woman-online-desktop.843eb114.svg);height:359px;width:472px;position:absolute;display:flex;align-items:center;right:15px}@media screen and (max-width: 920px){.FaqContainer .Row .Column.Img .Img::before{display:none}}.FaqContainer .Row .Column.Img .Img::after{content:url(/faq-accordion-card/static/media/illustration-box-desktop.ec1ba14b.svg);height:359px;width:472px;position:absolute;display:flex;align-items:center;right:15px;margin-top:110px}@media screen and (max-width: 920px){.FaqContainer .Row .Column.Img .Img::after{display:none}}.FaqContainer .Row .Column.Content{padding:65px 0}.FaqContainer .Row .Column.Content .QuestionAnswer{border-bottom:1px solid #e8e8ea;max-width:350px;width:100%}@media screen and (max-width: 920px){.FaqContainer .Row .Column.Content .QuestionAnswer{max-width:100%}}.FaqContainer .Row .Column.Content .QuestionAnswer .Question{cursor:pointer;padding:19px 0;display:flex;justify-content:space-between}.FaqContainer .Row .Column.Content .QuestionAnswer .Question::after{transition:transform .5s ease}.FaqContainer .Row .Column.Content .QuestionAnswer .Question>span{transition:color .5s ease}@media screen and (max-width: 920px){.FaqContainer .Row .Column.Content .QuestionAnswer .Question>span{font-size:13px}}.FaqContainer .Row .Column.Content .QuestionAnswer .Question:hover>span{color:#f47b56}.FaqContainer .Row .Column.Content .QuestionAnswer .Question.active::after{content:url(/faq-accordion-card/static/media/icon-arrow-down.852fa301.svg)}.FaqContainer .Row .Column.Content .QuestionAnswer .Question.hidden>span{font-weight:700}.FaqContainer .Row .Column.Content .QuestionAnswer .Question.hidden::after{content:url(/faq-accordion-card/static/media/icon-arrow-down.852fa301.svg);transform:rotate(180deg)}.FaqContainer .Row .Column.Content .QuestionAnswer .Answer{max-width:319px;transition:max-height .5s ease;overflow:hidden}.FaqContainer .Row .Column.Content .QuestionAnswer .Answer>span{font-size:12px;line-height:18px;color:#787887;padding-bottom:19px;display:block}.FaqContainer .Row .Column.Content .QuestionAnswer .Answer.active{max-height:100px}.FaqContainer .Row .Column.Content .QuestionAnswer .Answer.hidden{max-height:0}
